import { bindable, containerless } from 'aurelia-framework';

@containerless
export class EmChatChannelJoin {

    @bindable loginUser;

    _getChannels() {
        $.get('/admin/channel/list', (data) => {
            if (data.success) {
                this.channels = data.data;
                _.each(this.channels, (item) => {
                    item.joined = _.some(item.members, (m) => {
                        return m.username == this.loginUser.username;
                    });
                });
            } else {
                toastr.error(data.data, '!');
            }
        });
    }

    refresh() {
        this._getChannels();
    }

    joinHandler(item) {
        this.confirmMd.show({
            content: `<code class="nx">${item.title}</code>?`,
            onapprove: () => {
                $.post('/admin/channel/join', {
                    id: item.id
                }, (data) => {
                    if (data.success) {
                        toastr.success('!');
                        item.joined = true;
                        ea.publish(nsCons.EVENT_CHAT_CHANNEL_JOINED, { channel: data.data });
                    } else {
                        toastr.error(data.data, '!');
                    }
                });
            }
        });
    }

    leaveHandler(item) {
        this.confirmMd.show({
            content: `<code class="nx">${item.title}</code>?`,
            onapprove: () => {
                $.post('/admin/channel/leave', {
                    id: item.id
                }, (data) => {
                    if (data.success) {
                        toastr.success('!');
                        item.joined = false;
                        ea.publish(nsCons.EVENT_CHAT_CHANNEL_LEAVED, { channel: data.data });
                    } else {
                        toastr.error(data.data, '!');
                    }
                });
            }
        });
    }
}
